import React, { Component } from 'react'
import './ordersite.css'
class Ordersite extends Component {
  state = {
    address:[
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      },
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      },
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      },
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      },
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      },
      {
        name:'',
        ages:'909513',
        phone:'186***5194'
      }
    ],
    addressIndex:0
  }
  handleAdd1 = (i) => {
    this.setState({
      addressIndex:i
    })
  }
  render(){
    const add = this.state.address.map((item,i) => (
      <div className={`ordersite1 ${this.state.addressIndex===i&&'addresspic'}`} onClick={()=>this.handleAdd1(i)} key={i}>
        <p>
          <i className="news1">&#xe6ee;</i>{item.name}<br/>
        <i className="news1">&#xe618;</i>{item.ages}<br/>
      <i className="news1">&#xe6b9;</i>{item.phone}
        </p>
        <p>
          <a href="javascript:;"></a>
        <a href="javascript:;"> <i className="news1">&#xe69d;</i></a>
      <a className='clear' href="javascript:;"><i className="news1">&#xe608;</i></a>
    <a className='ordersite_moren' href="javascript:;"></a>
        </p>
        <p className="ordersite_remo">
          <a href="javascript:;"></a>
        <a href="javascript:;"></a>
        </p>
      </div>
    ))
    return(
      <div className="ordersite">
        {add}
        <div className="ordermore">
          <a href="javascript:;" className="order_more"></a>
        </div>
      </div>

    )
  }
}

export default Ordersite



