import React, { Component } from 'react'
import './ordertitle.css'
class Ordertitle extends Component {
  render(){
    return(
      <div className="ordertitle">
          <p className="ordertitle_left"></p>
          <p className='ordertitle_right'>
            <span></span>
            <span></span>
            <span>,</span>
          </p>
      </div>
    )
  }
}

export default Ordertitle



