import React, { Component } from 'react'
import './cartcon.css'
import { Link } from 'react-router-dom'

class Cartcon extends Component {
  render(){
    return(
      <div className="cartcon ">
        <ul>
          <li>
              <input type="checkbox" className="all" />
              <a href="javascript:;"></a>
            <a href="javascript:;" className="remo"></a>
          </li>
          <li>
              <span class="order">4</span><span class="order1">1</span>
          </li>
        </ul>
          <Link to="/order" className="close"></Link>
        <span><b className="total">0.00</b></span>
        <p>
            <br />[]
        </p>
      </div>
    )
  }
}

export default Cartcon



