import React, { Component } from 'react'
import './pay.css'
import img from "../images/success_03.png"
import { Link } from 'react-router-dom'
class Pay extends Component {
  render(){
    return(
      <div className="pay">
        <div className="pay_main">
        <div className="pay_up">
          <div className="pay_up_con1">
            <p className="pay_up_left"></p>
            <p className='pay_up_right'>
              <span></span>
              <span></span>
              <span>,</span>
            </p>
          </div>
          </div>
          <div className="pay_down">
          <div className="pay_suc_le">
              <img src={img} alt="" />
          </div>
          <div className="pay_suc_ri">
              <div className="pay_pay"></div>
              <div className="pay_other">
                  <span>12345678912345<br />:168.00</span>
                  <span>
                      <Link to="/add"></Link>
                    <Link className='pay_look' to="order"></Link>
                  </span>
              </div>
          </div>
        </div>
        </div>
      </div>
    )
  }
}

export default Pay



